import { graphql, Link } from "gatsby"
import { slugify } from "../../utils"
import React from "react"
import Layout from "../../components/layout"
import { Helmet } from "react-helmet"

export default function Blog({ data }) {
  const posts = data.allMarkdownRemark.edges
  return (
    <Layout>
      <Helmet>
        <title>My Blog - Sirwan Afifi</title>
      </Helmet>
      {posts.map(({ node }) => {
        const title = node.frontmatter.title || node.fields.slug
        return (
          <article key={slugify(node.frontmatter.title)}>
            <header>
              <h3 className="text-2xl font-black mt-16 mb-2">
                <Link
                  className="text-blue-600 shadow-none"
                  to={slugify(node.frontmatter.title)}
                >
                  {title}
                </Link>
              </h3>
              <div className="space-x-1 text-sm text-gray-500 mb-2">
                <time dateTime={node.frontmatter.date}>
                  {node.frontmatter.date}
                </time>
                <span aria-hidden="true">&middot;</span>
                <span>{node.timeToRead} min read</span>
              </div>
            </header>
            <section>
              <p
                className="mb-8"
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </section>
          </article>
        )
      })}
    </Layout>
  )
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { fields: frontmatter___date, order: DESC }) {
      edges {
        node {
          timeToRead
          frontmatter {
            title
            date(formatString: "MMMM Do, YYYY")
            slug
          }
          html
          excerpt
        }
      }
    }
  }
`
